@import "~antd/dist/antd.css";
@font-face {
  font-family: "jalnan";
  /* src: url("/src/assets/fonts/JalnanOTF.otf"); */
  src: url("https://tistory3.daumcdn.net/tistory/2981823/skin/images/JalnanOTF.otf");
}
* {
  font-family: jalnan;
}
.font-default,
.font-default > div > div > label > span {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}
.site-page-header {
  border: 1px solid rgb(235, 237, 240);
}

.text-best {
  color: #59a2f0;
}
.text-good {
  color: #288a4c;
}
.text-normal {
  color: #93a84f;
}
.text-soso {
  color: #d1a134;
}
.text-bad {
  color: #b42d2a;
}
.row-container {
  width: 410px;
  margin: 0 auto;
}
@media (max-width: 575.98px) {
  .row-container {
    width: 100%;
  }
}
